.chat {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;

  flex:4;
}

.center {
  position: absolute;
  top: 70px;
  left: 50%;
  transform: translatex(-50%);
  width: 90%;
  height: 75%;
  
}

.inputbox {
  background-color: gray;
  display: flex;
  height: 50px;
}

.inputbox .label1 {
  font-size: 1.5em;
  text-align: right;
}

.inputbox .text {
  font-size: 1em;
  padding-left: 8px;
  padding-right: 8px;
  flex: 1;
}

.inputbox .button {
  font-size: 1em;
  min-width: 40px;
}